body {
    font-family: Arial, Helvetica, sans-serif;
}

/* The Modal (background) */
.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    /* padding-top: 100px; Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0, 0, 0); /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 100%;
    height: 100%;
}

/* The Close Button */
.close {
    color: #aaaaaa;
    font-size: 28px;
    font-weight: bold;
    position: absolute;
    right: 8px;
    top: 0px;
}

.close:hover,
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.main-img {
    width: 100%;
}
.Main-login {
    /* padding: 24px; */
}
.main-img img {
    width: 110px;
}
.main-img p {
    font-size: 16px;
    color: #002f34;
    font-weight: 400;
}
.login-btns .icons {
    padding: 0px 3px;
    font-size: 17px;
}
.close span {
    font-size: 30px;
    cursor: pointer;
}
.login-btns button {
    color: #002f34;
    width: 92%;
    background-color: white;
    margin: 7px 0px;
    padding: 12px;
    font-weight: bold;
    font-family: sans-serif;
    border-radius: 5px;
    border: 2px solid black;
    cursor: pointer;
}
.login-btns button:hover {
    border: 4px solid black;
}
.para {
    font-size: 12px;
    font-weight: 500;
    padding-bottom: 10px;
}
.light {
    font-size: 12px;
    color: rgba(0, 47, 52, 0.64);
}
.light span {
    font-size: 12px;
    color: #007bff;
    cursor: pointer;
    padding-left: 2px;
}
.light span:hover {
    font-size: 12px;
    text-decoration: none;
    color: #007bff;
}

@media screen and (min-width: 950px) {
    .modal-content {
        width: 425px;
        height: 550px;
        border-radius: 5px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
