* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}
header {
    display: flex;
    align-items: center;
    background-color: #f7f8f8;
    height: 70px;
    width: 100%;
    border-bottom: 1px solid rgb(243, 241, 241);
    padding: 0px 25px;
    z-index: 1;
}
header .logo {
    height: 24px;
    margin-right: 25px;
    margin-left: 16px;
}
header .logo img {
    height: 100%;
}
header .location-box {
    height: 48px;
    display: flex;
    background-color: white;
    margin-right: 15px;
    border: 2px solid #002f34;
    outline: none;
    width: 250px;
}
header .location-box:hover {
    border: 2px solid #23e5db;
}
header .location-box input {
    width: 100%;
    outline: none;
    border: none;
}
header .location-box input:focus {
    /* border: 2px solid #23e5db; */
    outline: none;
    border: none;
}
header .location-box button {
    font-size: 20px;
    background-color: white;
    outline: none;
    border: none;
    padding: 10px;
}
header .location-box .icon {
    color: black;
    font-size: 20px;
    padding-left: 7px;
    padding-top: 8px;
}
header .search-box .icon {
    color: white;
    font-size: 26px;
    padding-left: 7px;
}

header .search-box {
    height: 48px;
    display: flex;
    max-width: 700px;
    width: 100%;
    margin-right: 25px;
    border: none;
}

header .search-box input:hover {
    border: 2px solid #23e5db;
}
header .search-box input {
    height: 100%;
    border-radius: 3px;
    width: 100%;
    border: 2px solid #002f34;
    padding: 0 20px;
    font-size: 16px;
}
header .search-box button {
    background-color: #002f34;
    height: 100%;
    width: 60px;
    margin-left: -5px;
}
header .search-box button i {
    color: white;
    font-size: 18px;
}
header .header-login {
    display: flex;
    align-items: center;
}
header .header-login u {
    margin-right: 30px;
    color: #002f34;
    font-size: 18px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bolder;
    cursor: pointer;
}
header .header-login u:hover {
    text-decoration: none;
}
header .header-btn {
    /* margin-right: 20px; */
    padding: 8px;
    border-radius: 20px;
    background-color: white;
    color: #002f34;
    width: 90px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bolder;
    cursor: pointer;
    outline: none;
}
header .header-btns {
    margin-left: 40px;
    display: flex;
}
header .icon {
    margin-right: 10px;
    color: #002f34;
}
header input::placeholder {
    color: rgb(141, 137, 137);
}

/* navbar */
nav ul {
    display: flex;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
nav li {
    list-style: none;
    padding: 10px;
    font-size: 14px;
    font-weight: 400;
}
nav li:hover {
    color: #23e5db;
}
nav .icon {
    font-size: 18px;
    margin-left: 7px;
}
li span {
    /* margin-left: 20px; */
    font-weight: bold;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

li span button {
    background-color: white;
    border: none;
    cursor: pointer;
}
li span button:focus {
    outline: none;
}
li span i {
    font-size: 18px;
    margin-left: 7px;
    margin-right: 18px;
}

.bac-image img {
    background-repeat: no-repeat;
    background-position: 100%;
    background-size: cover;
    width: 100%;
}
.ads img {
    width: 80%;
    padding: 2px;
}

.main-product img {
    width: 200px;
    height: 150px;
}

.main-box {
    width: 268px;
    /* margin: auto; */
    margin: 10px;
    position: relative;
    text-align: center;
    font-family: arial;
    background-color: white;
    border: 1px solid rgb(177, 175, 175);
}
.card-image {
    width: 100%;
    /* display: flex;
  align-items: flex-e; */
}
.card-image img {
    width: 150px;
    height: 150px;
    margin-top: 5px;
}
.price {
    height: 100px;
    border-left: 5px solid #ffce32;
    padding-top: 10px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.price h3 {
    text-align: left;
    padding-left: 20px;
    padding-bottom: 4px;
    color: #002f34;
    font-size: 20px;
}
.price p {
    text-align: left;
    padding-left: 20px;
    color: rgb(136, 135, 135);
}

.main-box button:hover {
    opacity: 0.7;
}

.cards {
    width: 600px;
    height: 200px;
}
header .cars {
    display: none;
}
.main-cards {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 100%;
}
.main-box .heart {
    width: 25px;
    height: 25px;
    position: absolute;
    right: 10px;
    top: 10px;
    cursor: pointer;
}
.main-box .heart img {
    width: 100%;
    height: 100%;
}
.main-box .heart i {
    font-size: 25px;
    margin: 0;
}
.Main-sell {
    background-color: #3a77ff;
    width: 100%;
    /* margin: 0 auto; */
    /* border-radius: 6px;
    padding-bottom: 22px;
    /* padding-top: 5px;  */
}
.main-sell-box {
    width: 256px;
    margin: 10px auto;

    height: 174px;
    text-align: center;
    font-family: sans-serif;
    color: white;
    display: flex;
    flex-direction: column;
}
.main-sell-box button {
    width: 100%;
    outline: none;
    cursor: pointer;
    background-color: #3a77ff;
    height: 30px;
    color: white;
    margin-top: 40px;
    font-family: sans-serif;
    border: 1px solid white;
}
.main-boxes {
    width: 90%;
    margin: 0 auto;
    border-radius: 6px;
    padding-bottom: 22px;
    padding-top: 5px;
}
.main-sell-box .btn {
    width: 200px;
}
.main-boxes .Cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.load {
    width: 95px;
    border: 2px solid black;
    outline: none;
    font-size: 14px;
    font-weight: 600;
    font-family: sans-serif;
    border-radius: 5px;
    height: 45px;
    background-color: white;
    color: black;
}
.main-text {
    font-size: 24px;
    padding-left: 22px;
    padding-top: 10px;
    color: #002f34;
    font-weight: 500;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
header .navigate {
    display: none;
}
header .current {
    display: none;
}
.main-categories {
    display: none;
}
a {
    text-decoration: none;
    color: #002f34;
}
.footer-ads {
    display: flex;
    background-color: #f7f8f8;
    font-family: sans-serif;
    justify-content: space-evenly;
    flex-wrap: wrap;
    color: #002f34;
}
.footer-ads .hr-line {
    width: 2px;
    margin: 31px;
    background-color: rgba(0, 47, 52, 0.2);
    border: 0 solid rgba(0, 47, 52, 0.2);
    height: 136px;
}
.footer-ads .ads-img img {
    width: 100%;
    height: 100%;
    /* margin-left: 30px; */
}
.ads-olx {
    width: 80%;
    margin-top: 20px;
    margin-bottom: 40px;
}
.ads-olx img {
    width: 100%;
}
.app-store {
    display: block;
    padding-top: 60px;
    margin-left: -60px;
}
.app-store .app-title span {
    /* display: block; */
    font-weight: bold;
}
.app-store img {
    margin-top: 16px;
    width: 128px;
    height: 40px;
    margin-right: 7px;
}

.title {
    padding-top: 28px;
    /* margin-right: -60px; */
}
.title p {
    font-size: 19px;
    /* font-weight: bold; */
}

.olx-footer {
    background-color: #ebeeef;
    border-top: 1px solid rgba(0, 47, 52, 0.2);
}
.olx-content li {
    list-style: none;
    padding: 6px 0px 0px 0px;
}
.olx-content p {
    font-family: sans-serif;
    font-weight: 600;
    color: #002f34;
    /* padding-bottom: 10px; */
    font-size: 14px;
}
.olx-content a {
    text-decoration: none;
    color: #002f34a3;
    font-family: sans-serif;
    font-size: 12px;
}
.olx-content ul {
    padding-top: 10px;
}
.olx-content {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    margin: 0px 80px;
}

.social-icon img {
    /* font-size: 10px; */
    width: 23px;
    height: 23px;
    padding-left: 2px;
    padding-top: 4px;
}

.social-icon .social-apps img {
    width: 80px;
    height: 25px;
    /* justify-content: flex-end; */
}
.social-icon .social-apps {
    justify-content: space-between;
    margin-top: 30px;
}
.company-footer {
    background-color: #002f34;
    padding: 20px;
    color: white;
    font-family: sans-serif;
    font-size: 12px;
    display: flex;
    justify-content: space-between;
}
.company-footer strong {
    font-weight: bold;
    font-family: sans-serif;
}
@media screen and (max-width: 731px) {
    header .search-box input {
        width: 100px;
    }
    header .location-box {
        width: 250px;
    }
    header .header-login {
        margin-left: -35px;
    }
    nav ul > li {
        font-size: 12px;
    }
    .hr-line {
        display: none;
    }
}

@media screen and (max-width: 631px) {
    header .header-login {
        display: none;
    }
    header .headeritems {
        display: flex;
        margin-top: 10px;
        margin-bottom: 20px;
    }

    header .hamburger {
        width: 35px;
        margin-left: -10px;
        font-size: 21px;
        margin-right: 7px;
    }
    header .location-box {
        display: none;
    }
    .olx-content {
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        text-align: center;
    }
    .olx-content section > p {
        padding-top: 10px;
    }
    .bac-image {
        display: none;
    }
    header .search-box {
        margin: 0 5px;
        order: 3;
        width: 100%;
        display: flex;
        align-self: flex-end;
        height: 30px;
        justify-content: center;
    }

    header .search-box input {
        width: 100%;

        white-space: nowrap;
    }
    .ads .img {
        display: none;
    }
    nav {
        display: none;
    }
    header .search-box button {
        display: none;
    }
    header .navigate {
        padding-left: 25px;
        display: block;
        font-size: 18px;
        font-weight: bolder;
        cursor: pointer;
    }
    header .current {
        margin-left: auto;
        margin-right: 5px;
        display: flex;
        color: #002f34;
        font-size: 14px;
        font-family: Arial, Helvetica, sans-serif;
    }

    header .cars button i {
        color: white;
        font-size: 18px;
    }

    header .current p {
        margin-right: 10px;
    }
    header {
        display: flex;
        height: initial;
        position: -webkit-sticky;
        position: sticky;
        top: 0;

        flex-wrap: wrap;
        padding: 5px 5px;
    }
    header .logo {
        margin-right: 10px;
    }

    /* mobile view icons */
    .icon .icon-image img {
        height: 60px;
        width: 60px;
        padding: 10px;
        border-radius: 50%;
    }
    .icon .icon-image > #clr-1 {
        background: #9bb9ff;
    }
    .icon .icon-image > #clr-2 {
        background: #f8dd3c;
    }
    .icon .icon-image > #clr-3 {
        background: #ffe894;
    }
    .icon .icon-image > #clr-4 {
        background: #ff645c;
    }
    .icon .icon-image > #clr-5 {
        background: #23e5d8;
    }
    .icon .icon-image > #clr-6 {
        background: #d2b982;
    }
    .icon .icon-image > #clr-7 {
        background: #8df1ee;
    }
    .icon .icon-image > #clr-8 {
        background: #cddcff;
    }
    .icon .icon-image > #clr-9 {
        background: #ff645c;
    }

    .icon .icon-image {
        text-align: center;
        width: 100%;
        height: 100%;

        display: inline-block;

        flex-wrap: wrap;
    }
    .icon .icon-image > p {
        font-size: 11px;
        color: #002f34;
        font-family: sans-serif;
        margin: 10px;
    }

    .main-categories {
        display: flex;
        flex-direction: column;
        margin: 0px 10px;
    }
    .categories .see-all u {
        color: #002f34;
        font-family: sans-serif;
    }
    .categories .see-all span {
        font-weight: bold;
        font-size: 12px;
    }
    .categories {
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;
    }
    .categories .browse {
        color: #002f34;
        font-family: sans-serif;
        font-weight: bold;
    }
    .icon {
        display: flex;
        flex-wrap: wrap;
    }
    .icon .inner-icons {
        margin: 10px auto;
        justify-content: space-around;
        flex-direction: column;
        display: flex;
    }
    .footer-ads {
        /* display: flex;
        flex-direction: column; */
        /* align-items: center; */
    }
    .app-store img {
        width: 100px;
        height: 40px;
    }
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
